.tree-view .MuiTreeItem-root {
  position: relative;
  padding-left: 1rem;
}

/* Set the font size for span elements within the tree */
.tree-view .MuiTypography-root,
.tree-view .MuiFormControlLabel-label,
.tree-view .MuiTreeItem-label {
  font-size: 0.8rem; /* Adjust as needed */
  /* font-weight: bold; */
}

/* Vertical line for the tree */
.tree-view .MuiTreeItem-group {
  padding-left: 1rem;
  /* border-left: 1px solid rgba(0, 0, 0, 0.5); */
  position: relative;
}
.MuiTreeItem-content {
  height: 2.5rem;
}

.MuiTypography-root.MuiTreeItem-label.MuiTypography-body1 {
  height: 2.5rem;
}

/* Horizontal line connecting to the parent */
.tree-view .MuiTreeItem-content:not(.no-before):before {
  content: "";
  position: absolute;
  /* top: 38%; */
  left: 0rem;
  top: 0.85rem;
  border-left: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
  height: 0.9rem; /* Adjust as needed */
  width: 1.5rem; /* Adjust to match the left margin of .MuiTreeItem-group */
  transform: translateY(-50%);
}

/* Ensure the label spans are displayed correctly */
.tree-view .MuiFormControlLabel-root {
  display: flex;
  align-items: center;
  justify-content: flex-start; /* Aligns text to the left */
  text-align: left; /* Ensures text within spans is left-aligned */
  width: fit-content; /* Ensures the flex container takes full width */
  position: relative;
  top: -0.08rem;
}

/* Uncomment and adjust if needed */
/* .MuiTreeItem-iconContainer {
  position: relative;
  top: 0;
} */

#disabled-div {
  pointer-events: none; /* Disable all interactions */
  opacity: 0.5; /* Dim the div to indicate it's disabled */
}

span > div > svg.MuiSvgIcon-root {
  /* background-color: black; */
  scale: 0.8;
}
